import React, { Component } from "react";
import Layout from "../../components/layout";
import { Form, Input, TextArea, Button, Select } from "semantic-ui-react";
const genderOptions = [
  { key: "m", text: "Technology", value: "male" },
  { key: "f", text: "HelathCare", value: "female" },
  { key: "m", text: "Sports", value: "male" },
  { key: "f", text: "Entertainment", value: "female" },
  { key: "f", text: "Travel", value: "female" }
];

class myclass extends Component {
  state = {
    mincontribution: ""
  };
  render() {
    return (
      <Layout>
        <h3>Create campaign smart contract</h3>
        <Form>
          <Form.Field
            id="form-input-control-first-name"
            control={Input}
            label="Campaign Description"
          />
          <Form.Field>
            <label>Minimum Contribution</label>
            <Input
              label="wei"
              labelPosition="right"
              value={this.state.mincontribution}
              onChange={event =>
                this.setState.mincontribution(event.target.value)
              }
            />
          </Form.Field>
          <Form.Field
            control={Select}
            options={genderOptions}
            label={{
              children: "Category",
              htmlFor: "form-select-control-gender"
            }}
            search
            searchInput={{ id: "form-select-control-gender" }}
          />
          <Form.Field
            id="form-button-control-public"
            control={Button}
            content="Create Campign"
            primary
          />
        </Form>
      </Layout>
    );
  }
}

export default myclass;
